export default (params) => {
  if (!params.target)
    return;
  const tabview = {
    node: params.target,
    tabs: params.target.appendChild(mapp.utils.html.node`<div class="tabs">`),
    tab
  };
  return tabview;
  function tab(tab2) {
    tab2.node = mapp.utils.html.node`<div class="tab">`;
    tab2.header = tab2.node.appendChild(mapp.utils.html.node`
      <div
        style="${tab2.tab_style || ""}"
        class="header"
        onclick=${tab2.show}>
        ${tab2.title || tab2.key || "Tab"}`);
    tab2.panel = tab2.node.appendChild(mapp.utils.html.node`
      <div
        class="${`panel ${tab2.class || ""}`}"
        style="${tab2.style || ""}">`);
    tab2.target && tab2.target instanceof HTMLElement && tab2.panel.appendChild(tab2.target);
    tab2.show = () => {
      tabview.tabs.childNodes.forEach((tab3) => tab3.classList.remove("active"));
      !tab2.node.parentElement && tabview.tabs.appendChild(tab2.node);
      tab2.node.classList.add("active");
      tabview.timer && window.clearTimeout(tabview.timer);
      tabview.timer = window.setTimeout(() => tab2.node.dispatchEvent(new CustomEvent("activate")), 500);
      if (tabview.node.classList.contains("desktop-display-none")) {
        tabview.node.classList.remove("desktop-display-none");
        document.body.style.gridTemplateRows = "auto 10px 50px";
      }
    };
    tab2.remove = () => {
      if (!tab2.node.parentElement)
        return;
      const sibling = tab2.node.nextElementSibling || tab2.node.previousElementSibling;
      tab2.node.remove();
      if (sibling)
        return sibling.querySelector(".header").click();
      tabview.node.classList.add("desktop-display-none");
      document.body.style.gridTemplateRows = "auto 0 0";
    };
  }
};
