export default (location) => {
  if (!location.infoj || location.infoj.length < 1)
    return;
  const listview = mapp.utils.html.node`<div class="location-view-grid">`;
  const groups = {};
  for (const entry of location.infoj) {
    if (location.view && location.view.classList.contains("disabled"))
      break;
    entry.listview = listview;
    entry.location = location;
    entry.type = entry.type || "text";
    entry.inline = entry.inline || entry.type === "integer" || entry.type === "numeric";
    entry.class = `${entry.class || ""} ${entry.type} ${entry.inline && "inline" || ""}`;
    if (entry.type === "key") {
      entry.listview.appendChild(mapp.utils.html.node`
        <div
          class="${entry.class}">
          <span
            title=${mapp.dictionary.location_source_layer}
            style="${`background-color: ${mapp.utils.chroma(location.style?.strokeColor || "#090").alpha(0.3)}`}">
              ${location.layer.name}`);
      continue;
    }
    entry.displayValue = entry.type === "numeric" ? parseFloat(entry.value).toLocaleString("en-GB", {maximumFractionDigits: 2}) : entry.type === "integer" ? parseInt(entry.value).toLocaleString("en-GB", {maximumFractionDigits: 0}) : entry.type === "date" ? mapp.ui.formatDate(entry.value) : entry.type === "datetime" ? mapp.ui.formatDateTime(entry.value) : entry.value;
    entry.displayValue = `${entry.prefix || ""}${entry.displayValue}${entry.suffix || ""}`;
    if (entry.group) {
      if (!groups[entry.group]) {
        groups[entry.group] = mapp.utils.html.node`
        <div
          class="${`drawer group panel expandable ${entry.class || ""}`}"
          style="${entry.group_css || ""}">
          <div
            class="header primary-colour"
            onclick=${(e) => {
          mapp.ui.toggleExpanderParent(e.target);
        }}>
            <span>${entry.group}</span>
            <span class="xyz-icon btn-header icon-expander primary-colour-filter">`;
        entry.listview.appendChild(groups[entry.group]);
      }
      entry.expanded && groups[entry.group].classList.add("expanded");
      entry.listview = groups[entry.group];
    }
    if (entry.plugin) {
      mapp.plugins[entry.plugin] && mapp.plugins[entry.plugin](entry);
      continue;
    }
    if (typeof entry.value !== "undefined" && entry.value === null && !entry.edit)
      continue;
    if (entry.title && !entry.query && !entry.dataviews) {
      if (!entry.edit && typeof entry.value === "undefined" && entry.type !== "title")
        continue;
      entry.listview.appendChild(mapp.utils.html.node`
        <div
          class="${`label ${entry.class}`}"
          style="${`${entry.css_title || ""}`}"
          title="${entry.tooltip || null}">${entry.title}`);
      if (entry.type === "title")
        continue;
    }
    if (mapp.ui.locations.view[entry.type]) {
      console.log(entry.type);
      mapp.ui.locations.view[entry.type](entry);
      continue;
    }
    if (!entry.query && !entry.edit && typeof entry.value === "undefined") {
      continue;
    }
    entry.val = entry.listview.appendChild(mapp.utils.html.node`
      <div
        class="${`val ${entry.class}`}"
        style="${`${entry.css_val || ""}`}">`);
    if (entry.query) {
      continue;
    }
    if (entry.edit && !entry.fieldfx) {
      mapp.ui.locations.view.edit.input(entry);
      continue;
    }
    if (entry.type === "html") {
      entry.val.innerHTML = entry.value;
      continue;
    }
    if (entry.displayValue == "null") {
      entry.val.remove();
      continue;
    }
    entry.val.textContent = entry.displayValue;
  }
  return listview;
};
